/** 获取没有副作用的数组 */
export function useDizhiList(start?: EarthlyBranch): DiZhiList {
  const _arr: DiZhiList = [
    new BranchZi(),
    new BranchChou(),
    new BranchYin(),
    new BranchMao(),
    new BranchChen(),
    new BranchSi(),
    new BranchWu(),
    new BranchWei(),
    new BranchShen(),
    new BranchYou(),
    new BranchXu(),
    new BranchHai(),
  ];
  const step = (start?.id || 1) - 1; // 默认从子开始
  const end = _arr.splice(0, step);
  _arr.push(...end);
  return _arr;
}
